<template>
  <div>
    &nbsp&nbsp
    <template v-if="filterParam.role=='and'">
      
    </template>
    <template v-else>
      
    </template>
    
    <br><br>
    <Table
      :columns="columns"
      :data="data"
      size="small"
      :show-header="false"
      no-data-text=""
      width="360"></Table>
  </div>
</template>

<script>
    export default {
      data () {
        return {
          columns: [
            {
              title: '',
              key: 'name',
              align:'center'
            },
            {
              title: '',
              key: 'role',
              align:'center'
            },
            {
              title: '',
              key: 'value',
              ellipsis:true,
              align:'center'
            }
          ]
        }
      },
      props:{
        filterParam:{
          default: function () {
            return {}
          }
        },
        tableColumnsData:Array
      },
      methods:{

      },
      computed:{
        //
        data:{
          get() {
            var filterItems = this.filterParam.filterData.domains
            var filterDatas = []
            var columnsData = this.tableColumnsData
            filterItems.forEach((value,index)=>{
              var colName = ""
              columnsData.forEach((col,jndex)=>{
                if(col["prop"] == value["role"][0]){
                  colName = col["label"]
                }
              })
              var roleName = ""
              switch (value["role"][1]){
                case 'equal': roleName=""
                  break
                case 'notEqual': roleName=""
                  break
                case 'contain': roleName=""
                  break
                case 'notContain': roleName=""
                  break
                case 'before': roleName=""
                  break
                case 'after': roleName=""
                  break
                case 'between': roleName=""
                  break
                case '=': roleName=""
                  break
                case '>': roleName=""
                  break
                case '<': roleName=""
                  break
                case 'include': roleName=""
                  break
                case 'isNull': roleName=""
                  break
                case 'notNull': roleName=""
                  break
              }
              var roleValues = ""
              value["value"].forEach((roleValue,index)=>{
                roleValues+=roleValue+","
              })
              var length = roleValues.length
              roleValues = roleValues.substr(0,length-1)
              filterDatas.push({
                name:colName,
                role:roleName,
                value:roleValues
              })
            })
            return filterDatas
          }
        }
      }
    }
</script>



