// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import { Input, Tree } from 'element-ui'
import Vuex from 'vuex'
import store from './vuex/store'
import iView from 'iview'
import util from './common/js/util'
import log from './common/js/log'
import 'iview/dist/styles/iview.css'
import 'element-ui/lib/theme-default/index.css'

//element-ui
Vue.use(Input)
Vue.use(Tree)
//iviewui
Vue.use(iView)
//vue-resource
Vue.use(VueResource)
//vuex
Vue.use(Vuex)
//vue-resource
// Vue.http.options.root = process.env.NODE_ENV === 'development' ? 'static' : ''
Vue.http.options.emulateJSON = true
//
Vue.config.productionTip = false
//log
Vue.prototype.$logHelper = log
//
Vue.prototype.$utilHelper = util
Vue.prototype.HOST = 'static'
//Vue.prototype.HOST = process.env.NODE_ENV === 'development'? '/api':''

let vm = new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',
  components: { App },
  render: h => h(App),
  methods:{
    //
    showErrorMessage(message){
      this.$Message.error(message)
    }
  },
  created(){
    //log,false
    this.$logHelper.openLog = true
    //Message 
    this.$Message.config({top: 50, duration: 2})
    //Notice 
    this.$Notice.config({top: 100, duration: 2})
    //Loading 
    this.$Loading.config({color: '#5cb85c', failedColor: '#f0ad4e', height: 3})
  }
})

//
router.beforeEach((to, from, next) => {
  //
  let haveRoute = false //
  vm.$router.options.routes.forEach((value,index)=>{
    if(to.path == value.path){
      haveRoute = true //
    }
  })
  if(haveRoute){
    //
    if(to.meta.requiresAuth){
      //
      if(window.sessionStorage.getItem("token",true)){
        next()
      }else{
        vm.showErrorMessage('!')
        next({path: '/'})
      }
    }else {
      //
      next()
    }
  }else {
    //
    vm.showErrorMessage('!')
    next(false)
  }
})

//vue-resource
Vue.http.interceptors.push(function(request, next) {
  //Loading
  this.$Loading.start()

  //
  if(Vue.prototype.HOST=="static"&&request.url.indexOf(".json")==-1){
    //
    request.method = "GET"
    request.url+=".json"
  }

  //
  var requestParams = request.body
  //,json
  request.body = {
    data:JSON.stringify(requestParams)
  }
  this.$logHelper.info("[requset data]---"+request.body.data)
  //
  next(function(response) {
    this.$logHelper.info("[response data]---"+JSON.stringify(response.body))
    //
    if(response.status == 200){
      //
      var code = response.body.code
      if(code != 200){
        //
        vm.showErrorMessage(response.body.message)
        //
        response.status = -1
        //Loading
        this.$Loading.error()
        switch (code){
          case 300:
            this.$router.push('/')
            break
        }
      }else{
        //Loading
        this.$Loading.finish()
      }
    }else{
      //
      vm.showErrorMessage('')
      this.$Loading.error()
    }
  })

})



